@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: "Indie Flower", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Box-layout {
  margin-top: 100px;
  display: inline-block;
  margin-right: 5px;
}

input {
  margin: 8px 0;
  padding: 5px 10px;
  box-sizing: border-box;
  border-radius: 4px;
  border: none;
  background-color: #f1f1f1;
}
label {
  font-weight: bold;
}
button {
  font-family: "Indie Flower", cursive;
  font-weight: bold;
  margin-top: 7px;
  text-align: center;
  padding: auto;
  border-radius: 4px;
  border: none;
  color: white;
  background-color: green;
}

.Boxlist {
  background-color: rgb(228, 187, 187);
  width: 500px;
  height: 300px;
  margin: auto;
  text-align: center;
}
h1 {
  margin-top: 50px;
  padding-top: 20px;
  color: rgba(0, 128, 128, 1);
  text-shadow: 0 0 3vw rgba(0, 128, 128, 0.6);
}

